<template>
  <ztree
    :setting="{
      async:{
        url: './data.json'
      },
      data: {
        key: {
            name: 'label'
        }
      }
    }"
    :data="[
    {
      label: '1 - ',
      open: true,
      children: [
        {
          label: '11 - ',
          children: [
            {
              label: '111'
            },
            {
              label: '112'
            },
            {
              label: '113'
            },
            {
              label: '114'
            }
          ]
        },
        {
          label: '12 - ',
          children: [
            {
              label: '121'
            },
            {
              label: '122'
            },
            {
              label: '123'
            },
            {
              label: '124'
            }
          ]
        },
        {
          label: '13 - ',
          isParent: true
        }
      ]
    }
  ]"
  >
  </ztree>
</template>
<script>
	import ztree from "ztreev";
	export default {
		components: {
			ztree
		}
	};
</script>
<style>
  @import "~ztree/css/metroStyle/metroStyle.css";
</style>


